.App {
  text-align: left;
}

.Table{
  margin-bottom:5rem;
}

.mountains{
  border-radius: 5%;
  width:100%;
  height:auto;
}
.Footer{
  background: #037BFE;
  color:white;
  border-radius: 15px 15px 0px 0px;
  margin-top: 1rem;
  padding: 1.5rem;
  left: 0;
  right:0;
  width: 100%;
  }


.Footer a {
  color:white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.custom-btn-toolbar {
  display: inline !important;
}

.custom-btn-toolbar > .btn {
  margin-right: 1rem;
}
ul{
  list-style-type: none;
}


ul:nth-child(1):before {
  background: green;
}

ul:nth-child(2):before {
  background: red;
}

ul:nth-child(3):before {
  background: blue;
}


.Walk-container {
  padding-left: 3em;
  padding-right: 3em;
  margin-top: 3em;
  padding-bottom: 3em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

